.header {
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  bottom: 0;
  width: $header-width;
  background-color: $white;
  @include md {
    left: 0;
    bottom: auto;
    width: auto;
    height: 50px;
    @include flex(flex-end); }
  &__burger {
    height: $header-width;
    // height: var(--header-width)
    @include flex(center, center);
    @include md {
      height: auto; } }
  &__btn {
    height: calc(100% - #{$header-width});
    // height: calc(100% - var(--header-width))
    display: block;
    @include md {
      height: auto; } } }

.burger {
  display: block;
  color: $black;
  width: 40px;
  height: 40px;
  .icon {
    font-size: 35px; } }

.header-btn {
  width: 100%;
  height: 100%;
  @include flex(flex-end, center, column);
  padding-bottom: 160px;
  background-color: $gray-lighten;
  &__title {
    display: block;
    transform: translate(calc(50% + .7em), 0) rotate(90deg);
    transform-origin: left top;
    white-space: nowrap; }
  &__icon {
    .icon {
      font-size: 24px; } } }
